<script>
    import {fade} from "svelte/transition";
    export let caseTitle;
    export let caseRole;
    export let caseDescription;
    export let caseExecution;
    export let caseLink;
    export let caseImage;
    export let caseTech;
    export let bonusLink;
    export let bonusLinkDescription;
    export let orange;
    export let blue;
    export let red;
    export let green;
    export let purple;
</script>

<article class:red class:blue class:green class:orange class:purple in:fade>
    {#if caseLink}
        <a href="{caseLink}" target="_blank">Link</a>
    {:else}
        <span class="nolink">No link available</span>
    {/if}
    <h2>{caseTitle}</h2>
    <h3>{caseRole}</h3>
    <img src="{caseImage}" alt="{caseTitle + ' case image'}" />
    <h4>Describe it!</h4>
    <p>{caseDescription}</p>
    <h4>What did you do?</h4>
    {#if bonusLink}
        <p>{caseExecution} <a class="bonusLink" href="{bonusLink}" target="_blank">{bonusLinkDescription}</a></p>
    {:else}
        <p>{caseExecution}</p>
    {/if}
    <h4>Tech?</h4>
    <p>{caseTech}</p>
</article>

<style>
    article {
        padding: 30px;
        box-shadow: 3px 3px 13px 13px rgba(0, 0, 0, 0.1);
        margin-bottom: 50px;
        background: #353535;
        background: linear-gradient(45deg, #333, #363636);
    }
    article:before {
        content: '';
        position: absolute;
        width: calc(100% - 65px);
        height: 4px;
        z-index: 9;
        margin-top: -30px;
        margin-left: -30px;
    }
    .orange:before {
        background: linear-gradient(to left, #f7ff00, #e58a00);
        background-size: 200% 200%;
        -webkit-animation: gradient-slide 7s ease infinite;
        -moz-animation: gradient-slide 7s ease infinite;
        animation: gradient-slide 7s ease infinite;
    }
    .blue:before {
        background: linear-gradient(to left, #1bffff, #1153c7);
        background-size: 200% 200%;
        -webkit-animation: gradient-slide 7s ease infinite;
        -moz-animation: gradient-slide 7s ease infinite;
        animation: gradient-slide 7s ease infinite;
    }
    .green:before {
        background: linear-gradient(to left, #fffb00, #489b6f);
        background-size: 200% 200%;
        -webkit-animation: gradient-slide 7s ease infinite;
        -moz-animation: gradient-slide 7s ease infinite;
        animation: gradient-slide 7s ease infinite;
    }
    .red:before {
        background: linear-gradient(to left, #ff9b00, #ff005d);
        background-size: 200% 200%;
        -webkit-animation: gradient-slide 7s ease infinite;
        -moz-animation: gradient-slide 7s ease infinite;
        animation: gradient-slide 7s ease infinite;
    }
    .purple:before {
        background: linear-gradient(to left, #ed1e79, #8b1ed3);
        background-size: 200% 200%;
        -webkit-animation: gradient-slide 7s ease infinite;
        -moz-animation: gradient-slide 7s ease infinite;
        animation: gradient-slide 7s ease infinite;
    }
    img {
        width: 100%;
        margin: 5px 0;
    }
    a, .nolink {
        float: right;
    }
    a.bonusLink {
        float: none;
    }
    h4 {
        font-weight: bold;
    }
    @-webkit-keyframes gradient-slide {
        0%{background-position:0% 54%}
        50%{background-position:100% 47%}
        100%{background-position:0% 54%}
    }
    @-moz-keyframes gradient-slide {
        0%{background-position:0% 54%}
        50%{background-position:100% 47%}
        100%{background-position:0% 54%}
    }
    @keyframes gradient-slide { 
        0%{background-position:0% 54%}
        50%{background-position:100% 47%}
        100%{background-position:0% 54%}
    }
</style>